.Player {
  flex: 1 1 1px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  font-size: 2.4em;
  border-top: 1px dashed;
}

.Player-name {
  margin-top: 20px;
}

.Player-life {
  font-size: 3em;
}

.Player-button {
  background-color: #fff;
  display: inline-block;
  position: relative;
  font-size: 33px;
  font-family: monospace;
  line-height: 48px;
  width: 74px;
  height: 74px;
  padding: 0.4em 0.8em;
  margin: 0.2em;
  margin-top: 0;
  box-sizing: border-box;
  border-radius: 6px;
}

.Game {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  position: relative;
}

.Game__players {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  position: relative;
}

/*------ 2 Players ------*/

.Game--players-2 .Player:nth-child(1) {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

/*------ 3 Players ------*/

.Game--players-3 .Game__players {
  flex-direction: row;
}

.Game--players-3 .Player {
  border-top: unset;
  border-right: 1px dashed #fff;
}

.Game--players-3 .Player:last-child {
  border: none;
}

.Game--players-3 .Menu {
  top: 5%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

/*------ Menu ------*/

.Menu {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: #fff;
}

.Menu button {
  font-size: 1.4em;
  line-height: 30px;
  min-width: 60px;
  height: 30px;
}

.Menu button + button {
  margin-left: 20px;
}

.Menu__options-button img {
  position: relative;
  top: 1px;
}

.RadioButton {
  width: 60px;
  height: 60px;
  background: rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: center;
  font-size: 1.6em;
  line-height: 62px;
}

.RadioButton--selected {
  background: rgb(203, 63, 152);
}

.Options {
  flex: 1 1 1px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 20px;
}

.Options__setting {
  text-align: center;
}

.Options__setting + .Options__setting {
  margin-top: 30px;
}

.Options__options-group {
  display: flex;
  justify-content: space-evenly;
}

.Options__confirm button {
  width: 80%;
  max-width: 268px;
  height: 50px;
  font-size: 20px;
  border-radius: 6px;
  margin: auto;
  display: block;
  background: #fff;
}

.App {
  background-color: #00d2ff;
  background: -webkit-gradient(linear, left top, right bottom, from(#00d2ff), to(#3a7bd5));
  background: linear-gradient(to bottom right, #00d2ff, #3a7bd5);
  height: 100%;
  color: #fff;
  display: flex;
  overflow: hidden;
}

button {
  position: relative;
  outline: 0;
  background-color: #fff;
  border: none;
  border-radius: 6px;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.4);
}

button:active {
  top: 1px;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  height: 100vh;
}

#root {
  height: 100%;
}
